

import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DebugElement,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Observable, of, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { reqAnimFrame } from 'tds-ui/core/animation';
import { isNotNil } from 'tds-ui/core/util';
import { DateHelperService, TDSI18nInterface, TDSI18nService } from 'tds-ui/i18n';
import { InputBoolean, TDSHelperObject, TDSSafeAny } from 'tds-ui/shared/utility';
import { TimeHolder } from './time-holder';

function makeRange(length: number, step: number = 1, start: number = 0): number[] {
  return new Array(Math.ceil(length / step)).fill(0).map((_, i) => (i + start) * step);
}

export type TDSTimePickerUnit = 'hour' | 'minute' | 'second' | '12-hour';

@Component({
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'tds-time-picker-panel',
  exportAs: 'tdsTimePickerPanel',
  templateUrl: './time-picker-panel.component.html',
  host: {
    class: "flex flex-col pr-0 border-box ",
    '[class.tds-picker-time-panel-column-0]': `enabledColumns === 0 && !inDatePicker`,
    '[class.tds-picker-time-panel-column-1]': `enabledColumns === 1 && !inDatePicker`,
    '[class.tds-picker-time-panel-column-2]': `enabledColumns === 2 && !inDatePicker`,
    '[class.tds-picker-time-panel-column-3]': `enabledColumns === 3 && !inDatePicker`,
    '[class.tds-picker-time-panel-narrow]': `enabledColumns < 3`,
    '[class.tds-picker-time-panel-placement-bottomLeft]': `!inDatePicker`,
    '(mousedown)': 'onMousedown($event)'
  },
  providers: [{ provide: NG_VALUE_ACCESSOR, useExisting: TDSTimePickerPanelComponent, multi: true }]
})
export class TDSTimePickerPanelComponent implements ControlValueAccessor, OnInit, OnDestroy, OnChanges {
  static ngAcceptInputType_use12Hours: BooleanInput;

  private _hourStep = 1;
  private _minuteStep = 1;
  private _secondStep = 1;
  private unsubscribe$ = new Subject<void>();
  private onChange?: (value: Date) => void;
  private onTouch?: () => void;
  private _format = 'hh:mm:ss a';
  private _disabledHours?: () => number[] = () => [];
  private _disabledMinutes?: (hour: number) => number[] = () => [];
  private _disabledSeconds?: (hour: number, minute: number) => number[] = () => [];
  private _allowEmpty = true;
  private _strAM: string = 'AM';
  private _strPM: string = 'PM';
  time = new TimeHolder();
  hourEnabled = true;
  minuteEnabled = true;
  secondEnabled = true;
  firstScrolled = false;
  enabledColumns = 3;
  hourRange!: ReadonlyArray<{ index: number; disabled: boolean }>;
  minuteRange!: ReadonlyArray<{ index: number; disabled: boolean }>;
  secondRange!: ReadonlyArray<{ index: number; disabled: boolean }>;
  use12HoursRange!: ReadonlyArray<{ index: number; value: string }>;
  select12HoursRange: Array<{ hour: number; minute: number; second: number; label: string; disabled: boolean }> = [];
  select12HoursRangeDefault: Array<{ index: number; value: string }> = [
    {
      index: 0,
      value: this._strAM
    },
    {
      index: 1,
      value: this._strPM
    }
  ];
  i18nTime$: Observable<string | undefined> = of(undefined);
  @ViewChild('hourListElement', { static: false })
  hourListElement?: DebugElement;
  @ViewChild('minuteListElement', { static: false }) minuteListElement?: DebugElement;
  @ViewChild('secondListElement', { static: false }) secondListElement?: DebugElement;
  @ViewChild('use12HoursListElement', { static: false }) use12HoursListElement?: DebugElement;

  @Input() inDatePicker: boolean = false; // If inside a date-picker, more diff works need to be done
  @Input() addOn?: TemplateRef<void>;
  @Input() hideDisabledOptions = false;
  @Input() clearText?: string;
  @Input() nowText?: string;
  @Input() okText?: string;
  @Input() placeHolder?: string | null;
  @Input() @InputBoolean() use12Hours = true;
  @Input() defaultOpenValue?: Date;

  @Output() readonly closePanel = new EventEmitter<void>();

  @Input()
  set allowEmpty(value: boolean) {
    if (isNotNil(value)) {
      this._allowEmpty = value;
    }
  }

  get allowEmpty(): boolean {
    return this._allowEmpty;
  }

  @Input()
  set disabledHours(value: undefined | (() => number[])) {
    this._disabledHours = value;
    if (!!this._disabledHours) {
      this.buildHours();
    }
  }

  get disabledHours(): undefined | (() => number[]) {
    return this._disabledHours;
  }

  @Input()
  set disabledMinutes(value: undefined | ((hour: number) => number[])) {
    if (isNotNil(value)) {
      this._disabledMinutes = value;
      this.buildMinutes();
    }
  }

  get disabledMinutes(): undefined | ((hour: number) => number[]) {
    return this._disabledMinutes;
  }

  @Input()
  set disabledSeconds(value: undefined | ((hour: number, minute: number) => number[])) {
    if (isNotNil(value)) {
      this._disabledSeconds = value;
      this.buildSeconds();
    }
  }

  get disabledSeconds(): undefined | ((hour: number, minute: number) => number[]) {
    return this._disabledSeconds;
  }

  @Input()
  set format(value: string) {
    if (isNotNil(value)) {
      this._format = value;
      this.enabledColumns = 0;
      const charSet = new Set(value);
      this.hourEnabled = charSet.has('H') || charSet.has('h');
      this.minuteEnabled = charSet.has('m');
      this.secondEnabled = charSet.has('s');
      if (this.hourEnabled) {
        this.enabledColumns++;
      }
      if (this.minuteEnabled) {
        this.enabledColumns++;
      }
      if (this.secondEnabled) {
        this.enabledColumns++;
      }
      if (this.use12Hours) {
        this.build12Hours();
      }
    }
  }

  get format(): string {
    return this._format;
  }

  @Input()
  set hourStep(value: number) {
    if (isNotNil(value)) {
      this._hourStep = value;
      this.buildHours();
    }
  }

  get hourStep(): number {
    return this._hourStep;
  }

  @Input()
  set minuteStep(value: number) {
    if (isNotNil(value)) {
      this._minuteStep = value;
      this.buildMinutes();
    }
  }

  get minuteStep(): number {
    return this._minuteStep;
  }

  @Input()
  set secondStep(value: number) {
    if (isNotNil(value)) {
      this._secondStep = value;
      this.buildSeconds();
    }
  }

  get secondStep(): number {
    return this._secondStep;
  }

  trackByFn(index: number): number {
    return index;
  }

  buildHours(): void {
    let hourRanges = 24;
    let disabledHours = this.disabledHours?.();
    let startIndex = 0;
    if (this.use12Hours) {
      hourRanges = 12;
      if (disabledHours) {
        if (this.time.selected12Hours === this._strPM) {
          /**
           * Filter and transform hours which greater or equal to 12
           * [0, 1, 2, ..., 12, 13, 14, 15, ..., 23] => [12, 1, 2, 3, ..., 11]
           */
          disabledHours = disabledHours.filter(i => i >= 12).map(i => (i > 12 ? i - 12 : i));
        } else {
          /**
           * Filter and transform hours which less than 12
           * [0, 1, 2,..., 12, 13, 14, 15, ...23] => [12, 1, 2, 3, ..., 11]
           */
          disabledHours = disabledHours.filter(i => i < 12 || i === 24).map(i => (i === 24 || i === 0 ? 12 : i));
        }
      }
      startIndex = 1;
    }
    this.hourRange = makeRange(hourRanges, this.hourStep, startIndex).map(r => {
      return {
        index: r,
        disabled: !!disabledHours && disabledHours.indexOf(r) !== -1
      };
    });
    if (this.use12Hours && this.hourRange[this.hourRange.length - 1].index === 12) {
      const temp = [...this.hourRange];
      temp.unshift(temp[temp.length - 1]);
      temp.splice(temp.length - 1, 1);
      this.hourRange = temp;
    }
  }

  buildMinutes(): void {
    this.minuteRange = makeRange(60, this.minuteStep).map(r => {
      return {
        index: r,
        disabled: !!this.disabledMinutes && this.disabledMinutes(this.time.hours!).indexOf(r) !== -1
      };
    });
  }

  buildSeconds(): void {
    this.secondRange = makeRange(60, this.secondStep).map(r => {
      return {
        index: r,
        disabled: !!this.disabledSeconds && this.disabledSeconds(this.time.hours!, this.time.minutes!).indexOf(r) !== -1
      };
    });
  }

  build12Hours(): void {
    const local = this.i18n.getLocale();

    const isUpperFormat = this._format.includes('A');
    if (local.locale === 'vi') {
      this._strAM = 'SA';
      this._strPM = 'CH';
      this.time.setStrAM('SA');
      this.time.setStrPM('CH');
    }
    this.use12HoursRange = [
      {
        index: 0,
        value: this._strAM
      },
      {
        index: 1,
        value: this._strPM
      }
    ];
    this.select12HoursRangeDefault = [
      {
        index: 0,
        value: this._strAM
      },
      {
        index: 1,
        value: this._strPM
      }
    ];
  }
  buildSelect12Hours(): void {
    this.select12HoursRange = [];
    let array: Array<TDSSafeAny> = [];

    if (this.use12Hours && TDSHelperObject.hasValue(this.hourRange)) {
      this.hourRange.forEach(h => {
        array.push({
          hour: h.index,
          minute: 0,
          second: 0,
          disabled: h.disabled,
          label: `${h.index}:00`
        });
        array.push({
          hour: h.index,
          minute: 30,
          second: 0,
          disabled: h.disabled,
          label: `${h.index}:30`
        });
      });
      this.select12HoursRange = array.sort((a, b) => a.hour - b.hour)
    }
  }
  buildTimes(): void {
    this.buildHours();
    this.buildMinutes();
    this.buildSeconds();
    this.build12Hours();
    this.buildSelect12Hours();
  }

  scrollToTime(delay: number = 0): void {
    if (this.hourEnabled && this.hourListElement) {
      this.scrollToSelected(this.hourListElement.nativeElement, this.time.viewHours!, delay, 'hour');
    }
    if (this.minuteEnabled && this.minuteListElement) {
      this.scrollToSelected(this.minuteListElement.nativeElement, this.time.minutes!, delay, 'minute');
    }
    if (this.secondEnabled && this.secondListElement) {
      this.scrollToSelected(this.secondListElement.nativeElement, this.time.seconds!, delay, 'second');
    }
    if (this.use12Hours && this.use12HoursListElement) {
      const selectedHours = this.time.selected12Hours;
      const index = selectedHours === this._strAM ? 0 : 1;
      this.scrollToSelected(this.use12HoursListElement.nativeElement, index, delay, '12-hour');
    }
  }

  selectHour(hour: { index: number; disabled: boolean }): void {
    this.time.setHours(hour.index, hour.disabled);
    if (!!this._disabledMinutes) {
      this.buildMinutes();
    }
    if (this._disabledSeconds || this._disabledMinutes) {
      this.buildSeconds();
    }
  }

  selectMinute(minute: { index: number; disabled: boolean }): void {
    this.time.setMinutes(minute.index, minute.disabled);
    if (!!this._disabledSeconds) {
      this.buildSeconds();
    }
  }

  selectSecond(second: { index: number; disabled: boolean }): void {
    this.time.setSeconds(second.index, second.disabled);
  }

  select12Hours(value: { index: number; value: string }): void {
    const select12h = this.select12HoursRangeDefault.find(f => f.index == value.index)?.value;
    this.time.setSelected12Hours(select12h);
    if (!!this._disabledHours) {
      this.buildHours();
    }
    if (!!this._disabledMinutes) {
      this.buildMinutes();
    }
    if (!!this._disabledSeconds) {
      this.buildSeconds();
    }
  }
  selectCutom12Hours(data: { hour: number; minute: number; second: number; label: string; disabled: boolean }, str: { index: number; value: string }) {

    const select12h = this.select12HoursRangeDefault.find(f => f.index == str.index)?.value;
    this.time.setHours(data.hour, data.disabled);
    this.time.setMinutes(data.minute, data.disabled);
    this.time.setSeconds(data.second, data.disabled);
    this.time.setSelected12Hours(select12h);
    if (!!this._disabledHours) {
      this.buildHours();
    }
    if (!!this._disabledMinutes) {
      this.buildMinutes();
    }
    if (!!this._disabledSeconds) {
      this.buildSeconds();
    }
  }
  scrollToSelected(instance: HTMLElement, index: number, duration: number = 0, unit: TDSTimePickerUnit): void {
    if (!instance) {
      return;
    }
    const transIndex = this.translateIndex(index, unit);
    const currentOption = (instance.children[transIndex] || instance.children[0]) as HTMLElement;
    this.scrollTo(instance, currentOption.offsetTop, duration);
  }

  translateIndex(index: number, unit: TDSTimePickerUnit): number {
    if (unit === 'hour') {
      return this.calcIndex(this.disabledHours?.(), this.hourRange.map(item => item.index).indexOf(index));
    } else if (unit === 'minute') {
      return this.calcIndex(this.disabledMinutes?.(this.time.hours!), this.minuteRange.map(item => item.index).indexOf(index));
    } else if (unit === 'second') {
      // second
      return this.calcIndex(
        this.disabledSeconds?.(this.time.hours!, this.time.minutes!),
        this.secondRange.map(item => item.index).indexOf(index)
      );
    } else {
      // 12-hour
      return this.calcIndex([], this.use12HoursRange.map(item => item.index).indexOf(index));
    }
  }

  scrollTo(element: HTMLElement, to: number, duration: number): void {
    if (duration <= 0) {
      element.scrollTop = to;
      return;
    }
    const difference = to - element.scrollTop;
    const perTick = (difference / duration) * 10;

    reqAnimFrame(() => {
      element.scrollTop = element.scrollTop + perTick;
      if (element.scrollTop === to) {
        return;
      }
      this.scrollTo(element, to, duration - 10);
    });
  }

  calcIndex(array: number[] | undefined, index: number): number {
    if (array?.length && this.hideDisabledOptions) {
      return (
        index -
        array.reduce((pre, value) => {
          return pre + (value < index ? 1 : 0);
        }, 0)
      );
    } else {
      return index;
    }
  }

  protected changed(): void {
    if (this.onChange) {
      this.onChange(this.time.value!);
    }
  }

  protected touched(): void {
    if (this.onTouch) {
      this.onTouch();
    }
  }

  timeDisabled(value: Date): boolean {
    const hour = value.getHours();
    const minute = value.getMinutes();
    const second = value.getSeconds();
    return (
      (this.disabledHours?.().indexOf(hour) ?? -1) > -1 ||
      (this.disabledMinutes?.(hour).indexOf(minute) ?? -1) > -1 ||
      (this.disabledSeconds?.(hour, minute).indexOf(second) ?? -1) > -1
    );
  }

  onClickNow(): void {
    const now = new Date();
    if (this.timeDisabled(now)) {
      return;
    }
    this.time.setValue(now);
    this.changed();
    this.closePanel.emit();
  }

  onClickOk(): void {
    this.closePanel.emit();
  }

  isSelectedHour(hour: { index: number; disabled: boolean }): boolean {
    return hour.index === this.time.viewHours;
  }

  isSelectedMinute(minute: { index: number; disabled: boolean }): boolean {
    return minute.index === this.time.minutes;
  }

  isSelectedSecond(second: { index: number; disabled: boolean }): boolean {
    return second.index === this.time.seconds;
  }

  // isSelected12Hours(value: { hour: number; minute: number; second: number; label: string; disabled: boolean }, range: { index: number; value: string }): boolean {
  //   const select12h = this.select12HoursRangeDefault.find(f => f.index == range.index)?.value?.toUpperCase();
  //   return value.hour === this.time.viewHours
  //     && value.minute === this.time.minutes
  //     && value.second === this.time.seconds
  //     && select12h === this.time.selected12Hours?.toUpperCase();
  // }
  isSelected12Hours(value: { index: number; value: string }): boolean {
    const select12h = this.select12HoursRangeDefault.find(f => f.index == value.index)?.value?.toUpperCase();
    return select12h === this.time.selected12Hours?.toUpperCase();
  }
  constructor(
    private cdr: ChangeDetectorRef,
    public dateHelper: DateHelperService,
    private elementRef: ElementRef,
    protected i18n: TDSI18nService,) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-picker-time-panel');
  }

  ngOnInit(): void {
    this.time.changes.pipe(takeUntil(this.unsubscribe$)).subscribe(() => {
      this.changed();
      this.touched();
      this.scrollToTime(120);
    });
    this.buildTimes();
    setTimeout(() => {
      this.scrollToTime();
      this.firstScrolled = true;
    });

    this.i18nTime$ = this.i18n.localeChange.pipe(map((locale: TDSI18nInterface) => locale.TimePicker.time));
  }

  ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { use12Hours, defaultOpenValue } = changes;
    if (!use12Hours?.previousValue && use12Hours?.currentValue) {
      this.build12Hours();
      this.enabledColumns++;
    }
    if (defaultOpenValue?.currentValue) {
      this.time.setDefaultOpenValue(this.defaultOpenValue || new Date());
    }
  }

  writeValue(value: Date): void {
    this.time.setValue(value, this.use12Hours);
    this.buildTimes();

    if (value && this.firstScrolled) {
      this.scrollToTime(120);
    }
    // Mark this component to be checked manually with internal properties changing (see: https://github.com/angular/angular/issues/10816)
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (value: Date) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  /**
   * Prevent input losing focus when click panel
   * @param event
   */
  onMousedown(event: MouseEvent): void {
    event.preventDefault();
  }
}
